<template>
  <div class="lineUnderCoupons">
    <van-nav-bar
      title="线下优惠券"
      @click-left="$toPage(-1)"
      >
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left" />
    </van-nav-bar>
    <van-cell-group>
      <van-field
        v-model="couponsCode"
        clearable
        size="large"
        label="券面明码"
        placeholder="请输入券面明码"
      />

      <van-field
        v-model="couponsPwd"
        size="large"
        type="password"
        label="密码"
        placeholder="请输入密码"
      />
    </van-cell-group>
    <van-button type="danger" block @click="commit">确认</van-button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      couponsCode: '',
      couponsPwd: ''
    }
  },
  computed: {
    userInfo() {
      return this.$loadUserInfo();
    }
  },
  methods: {
    commit() {
      JNB.query({
        serverName: "discountServer",
        cls: "CouponManageModule",
        method: "revOfflineExcgCoupon",
        params: {
          compid: this.userInfo.comp.storeId,
          exno: this.couponsCode,
          pwd: this.couponsPwd
        },
        callback: data => {
          let result = JSON.parse(data);

          if (result.code === 200) {
            this.$toast.success('领取成功！');
            this.$router.push({
              path: '/coupons'
            })
          } else {
            this.$toast.fail(result.message);
          }
        }
      });
    }
  }
}
</script>
<style scoped>
.lineUnderCoupons .van-button{
  width: 710px;
  font-size: 28px;
  position: absolute;
  bottom: 10px;
  left: 20px;
  border-radius: 20px;
}
</style>
